import { useEffect, useState, useReducer } from "react";

const App = () => {
  const [students, setStudents] = useState([
    { name: "Student 1", passed: false, edit: false, randomChoise: false },
    { name: "Student 2", passed: false, edit: false, randomChoise: false },
    { name: "Student 3", passed: false, edit: false, randomChoise: false },
    { name: "Student 4", passed: false, edit: false, randomChoise: false },
    { name: "Student 5", passed: false, edit: false, randomChoise: false },
    { name: "Student 6", passed: false, edit: false, randomChoise: false },
    { name: "Student 7", passed: false, edit: false, randomChoise: false },
    { name: "Student 8", passed: false, edit: false, randomChoise: false },
    { name: "Student 9", passed: false, edit: false, randomChoise: false },
    { name: "Student 10", passed: false, edit: false, randomChoise: false },
    // { name: "Student 9", passage: false, randomChoise: false },
    // { name: "Student 10", passage: false, randomChoise: false },
    // { name: "Student 11", passage: false, randomChoise: false },
    // { name: "Student 12", passage: false, randomChoise: false },
  ]);
  const [studentPassed, setStudentPassed] = useState([]);
  const [changeN, setChangeN] = useState("");
  const [, forceUpdate] = useReducer(x => x + 1, 0);
  useEffect(() => {
  }, [students])

  let handleChange = (event, i) => {
    setChangeN(event.target.value)
  }

  let changeName = (i) => {
    console.log("test");
    let studentRandom = [...students];

    if (studentRandom[i].edit === false) {
      studentRandom.forEach(e => {
        e.edit = false;
      })
      studentRandom[i].edit = true;
      setStudents(studentRandom)
      return setChangeN(studentRandom[i].name);
    }

    if (studentRandom[i].edit === true) {
      studentRandom[i].name = changeN;
      studentRandom[i].edit = false;
      setStudents(studentRandom)
      return setChangeN("");
    }
  }

  // let reloadRandom = () => {
  //   let studentRandom = [...students];
  //   let studentRandomPassed = [...studentPassed];

  //   for (let i = 0; i < studentRandom.length; i++) {
  //     studentRandom[i].passed = 0;
  //     studentRandom[i].randomChoise = 0;
  //   }

  //   for (let i = 0; i < studentRandomPassed.length; i++) {
  //     studentRandomPassed[i].edit = 0;
  //     studentRandom.push(studentRandomPassed[i]);
  //     studentRandomPassed.splice(i, 1);
  //     i--;
  //   }
  //   setStudents(studentRandom);
  //   setStudentPassed(studentRandomPassed);

  //   console.log(students);
  // }


  let startRandom = () => {
    let studentRandom = [...students];
    let studentRandomPassed = [...studentPassed];
    studentRandom.forEach((e, i) => {
      if (e.passed === true) {
        studentRandomPassed.push(e)
        studentRandom.splice(i, 1)
        setStudentPassed(studentRandomPassed)
        forceUpdate()
      }
    })
    for (let i = 0; i < 40; i++) {
      let count = i + "000"
      setTimeout(() => {
        let random = Math.floor(Math.random() * studentRandom.length)
        studentRandom.forEach(elem => {
          elem.randomChoise = false;
        })
        studentRandom[random].randomChoise = true;

        if (i === 39) {
          studentRandom[random].passed = true
        }
        setStudents(studentRandom);
        forceUpdate()
      }, parseInt(count) / 7);
    }
  }

  return (
    <div className="container my-5">
      <h1 className="text-center my-5">Randomiser #CodingWeek25</h1>
      <div className="row">
        {students.map((e, i) => {
          return (
            <div key={i} className="col-12 col-md-3">
              <div className={`card my-2 ${e.passed === true ? "bg-success" : e.randomChoise === true ? "bg-warning" : "bg-purple"} d-flex align-items-center justify-content-center`}>
                {(() => {
                  if (e.edit === false) {
                    return <p className="m-0" onClick={() => changeName(i)}>{e.name}</p>
                  } else {
                    return <input className="w-75 text-center transparent" type="text" value={changeN} onChange={(event) => handleChange(event, i)} onKeyPress={(event) => {
                      if (event.key === "Enter") {
                        changeName(i)
                      }
                    }} />
                  }
                })()}
              </div>
            </div>
          )
        })}
      </div>
      <div className="d-flex justify-content-center mt-3">
        {(() => {
          if (students.length > 1) {
            return <button className="btn btn-primary text-white" onClick={startRandom}>Random</button>
          }
        })()}
        {/* <button className="btn bg-purple text-white mx-2" onClick={reloadRandom}>Reload</button> */}
      </div>
      <h1 className="text-center my-5">Passed</h1>
      <div className="row">

        {studentPassed.map((e, i) => {
          return (
            <div key={i} className="col-3">
              <div className={`card my-2 bg-success d-flex align-items-center justify-content-center`}>
                {e.name}
              </div>
            </div>
          )
        })}
      </div>

    </div>
  );
}

export default App;
